import React from "react";
import { Button, Card, Tabs, Tab } from "react-bootstrap";
import { Github } from "react-bootstrap-icons";

interface DataProps {
	TapTitle: string;
	CardTitle: string;
	CardText: string;
  Link?: string;
  Github?: string;
}

interface ProCardProps {
	Data: DataProps[];
}

const ProCard = ({ Data }: ProCardProps) => {
	console.log(Data);
	return (
		<Card>
			<Card.Header>
				<Tabs
					defaultActiveKey={Data[0].TapTitle}
					id="projects"
					className="mb-3"
					variant="pills"
				>
					{Data.map((data: any, index: number) => (
						<Tab
							eventKey={data.TapTitle.toLowerCase()}
							title={data.TapTitle}
							key={index}
						>
							<Card.Body>
								<Card.Title>{data.CardTitle} jónas</Card.Title>
								<Card.Text>{data.CardText}jónas</Card.Text>
								{data.Github && <Github size={25}/>}
                {data.Link && <Github size={25}/>}
							</Card.Body>
						</Tab>
					))}
				</Tabs>
			</Card.Header>
		</Card>
	);
};

export default ProCard;
